<template>

    <div>

        <el-tag type="warning"
                style="margin-left: 20px;margin-bottom: 10px;width: 200px;height: 40px;text-align: center;line-height: 40px;font-size: 20px">
            财务报表
        </el-tag>
        <el-col :span="23" style=" padding-bottom: 30px;">

            <el-row>
                <el-col :span="2" class="el-col" style="margin-left: 0">
                    <p class="input-title">手机号:</p>
                    <el-input v-model="searchForm.contactPhone" placeholder="手机号">
                    </el-input>
                </el-col>
                <el-col :span="2" class="el-col" style="margin-left: 20px">
                    <p class="input-title">订单号:</p>
                    <el-input v-model="searchForm.serailId" placeholder="订单号">
                    </el-input>
                </el-col>
                <el-col :span="3" class="el-col" style="margin-left: 20px">
                    <p class="input-title">供应商订单号:</p>
                    <el-input v-model="searchForm.supSerialId" placeholder="供应商订单号">
                    </el-input>
                </el-col>
                <el-col :span="3" class="el-col" style="margin-left: 20px">
                    <p class="input-title">供应商名称:</p>
                    <el-select v-model="searchForm.supplierId"  placeholder="请选择">
                        <el-option
                            v-for="item in supplierOptions"
                            :key="item.supplierId"
                            :label="item.supplierName"
                            :value="item.supplierId">
                        </el-option>
                    </el-select>

                </el-col>
                <el-col :span="3" class="el-col" style="margin-left: 20px">
                    <p class="input-title">出发站:</p>
                    <el-select v-model="searchForm.depStation"  placeholder="请选择">
                        <el-option
                            v-for="item in depStationlist"
                            :key="item.depStation"
                            :label="item.depStation"
                            :value="item.depStation">
                        </el-option>
                    </el-select>

                </el-col>
                <el-col :span="2" class="el-col" style="margin-left: 20px">
                    <p class="input-title">支付流水号:</p>
                    <el-input v-model="searchForm.tradeSerialId" placeholder="支付流水号">
                    </el-input>
                </el-col>
                <el-col :span="3" class="el-col" style="margin-left: 20px">
                    <p class="input-title">收款状态:</p>
                    <el-select v-model="searchForm.payStatus"  placeholder="请选择">
                        <el-option
                            v-for="item in payStatusOption"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                    </el-select>

                </el-col>
                <el-col :span="2" class="el-col" style="margin-left: 20px;">
                    <p class="input-title">票状态:</p>
                    <el-select v-model="searchForm.ticketStatus"  placeholder="请选择">
                        <el-option
                            v-for="item in ticketStatusOption"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                    </el-select>

                </el-col>
                <el-col :span="11" class="el-col" style="margin-top: 20px">
                    <p class="input-title">支付时间：</p>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.startPayTime"
                        type="datetime"
                        placeholder="开始时间"
                        value-format="yyyy-MM-dd HH:mm:ss"
                       >
                    </el-date-picker>
                    <span style="float: left;line-height: 40px">&nbsp;&nbsp;至&nbsp;&nbsp;</span>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.endPayTime"
                        type="datetime"
                        placeholder="结束时间"
                        value-format="yyyy-MM-dd HH:mm:ss"

                    >
                    </el-date-picker>
                </el-col>
                <!--startDepTime:"",-->
                <!--endDepTime:"",-->
                <!--startValidateTime:"",-->
                <!--endValidateTime:"",-->
                <!--startRefundTime:"",-->
                <!--endRefundTime:"",-->
                <el-col :span="11" class="el-col" style="margin-top: 20px">
                    <p class="input-title">退款时间：</p>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.startRefundTime"
                        type="datetime"
                        placeholder="开始时间"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        >
                    </el-date-picker>
                    <span style="float: left;line-height: 40px">&nbsp;&nbsp;至&nbsp;&nbsp;</span>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.endRefundTime"
                        type="datetime"
                        placeholder="结束时间"
                        value-format="yyyy-MM-dd HH:mm:ss"

                    >
                    </el-date-picker>
                </el-col>
                <el-col :span="11" class="el-col" style="margin-top: 20px">
                    <p class="input-title">发车时间：</p>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.startDepTime"
                        type="datetime"
                        placeholder="开始时间"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        >
                    </el-date-picker>
                    <span style="float: left;line-height: 40px">&nbsp;&nbsp;至&nbsp;&nbsp;</span>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.endDepTime"
                        type="datetime"
                        placeholder="结束时间"
                        value-format="yyyy-MM-dd HH:mm:ss"

                    >
                    </el-date-picker>
                </el-col>
                <el-col :span="11" class="el-col" style="margin-top: 20px">
                    <p class="input-title">核验时间：</p>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.startValidateTime"
                        type="datetime"
                        placeholder="开始时间"
                        value-format="yyyy-MM-dd HH:mm:ss"
                      >
                    </el-date-picker>
                    <span style="float: left;line-height: 40px">&nbsp;&nbsp;至&nbsp;&nbsp;</span>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.endValidateTime"
                        type="datetime"
                        placeholder="结束时间"
                        value-format="yyyy-MM-dd HH:mm:ss"

                    >
                    </el-date-picker>
                </el-col>
            </el-row>
        </el-col>
        <el-col :span="23" style=" padding-bottom: 30px;border-bottom: 1px solid gainsboro">


            <el-col :span="1.5" class="el-col" style="float: right;margin-top: -50px">
                <p class="input-title">&nbsp;</p>
                <el-button type="primary" @click="orderExport">导出明细</el-button>
            </el-col>
            <el-col :span="1.5" class="el-col" style="float: right;margin-top: -50px">
                <p class="input-title">&nbsp;</p>
                <el-button type="primary" @click="clear">重置</el-button>
            </el-col>
            <el-col :span="1.5" class="el-col" style="float: right;margin-top: -50px">
                <p class="input-title">&nbsp;</p>
                <el-button type="primary" icon="el-icon-search" v-loading.fullscreen.lock="fullscreenLoading"
                           @click="search">查询
                </el-button>
            </el-col>

        </el-col>

        <el-col :span="23" style=" padding-top: 30px;">
            <table class="content-table">
                <tr>

                    <th>用户名</th>
                    <th>下单手机号</th>
                    <th>订单号</th>
                    <th>供应商订单号</th>
                    <th>供应商名称</th>
                    <th>出发站</th>
                    <th>支付流水号</th>
                    <th>支付时间</th>
                    <th>收款状态</th>
                    <th>票状态</th>
                    <th>付款金额</th>


                </tr>
                <tr v-for="(item,index) in listdata.data">
                    <td>{{item.contactName}}</td>
                    <td>{{item.contactPhone}}</td>
                    <td> {{item.serailId}}</td>
                    <td>{{item.supSerialId}}</td>
                    <td>{{item.supplierName}}</td>
                    <td>{{item.depStation}}</td>
                    <td>{{item.tradeSerialId}}</td>
                    <td>{{item.payTime}}</td>
                    <td>{{item.payStatusText}}</td>
                    <td>{{item.ticketText}}</td>
                    <td>{{item.amount}}</td>
                </tr>
                <tr>
                    <td colspan="11" style="text-align: left;background: ghostwhite">
                        &nbsp; &nbsp; &nbsp;
                        总金额 : {{listdata.totalAmount}}元，&nbsp;
                        退款金额 : {{listdata.refundAmount}}元，&nbsp;
                        总票数 : {{listdata.totalTicketCount}},&nbsp;
                        退款票数 : {{listdata.refundTicketCount}},&nbsp;
                    </td>
                </tr>
            </table>
            <div class="block page-f" style="position: relative;width: 100%;">

                <el-pagination
                    style="position: absolute;right: 0"
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage"
                    :page-size="pageSize"
                    :page-sizes="[10, 20, 30, 40]"
                    layout="total,prev, pager, next, jumper"
                    :total="listdata.totalCount">
                </el-pagination>
            </div>
            <div style="height: 80px"></div>
        </el-col>


    </div>
</template>
<script>
    import ajax from "../model/sys/index"

    const cityOptions = [1, 2, 3, 4, 5, 6, 7];

    export default {
        data() {
            return {
                checkAll: false,
                checkedCities: [],
                cities: cityOptions,
                timeValue: '',
                currentPage: 1,//默认页码
                pageSize: 10,//每页显示条数
                listdata: "",
                depStationlist:"",
                supplierOptions:"",
                searchForm: {
                    contactName:"",
                    contactPhone:"",
                    serailId:"",
                    supSerialId:"",
                    depStation:"",
                    payChannel:"",
                    tradeSerialId:"",
                    startPayTime:"",
                    endPayTime:"",
                    payStatus:"",
                    ticketStatus:"",
                    validateTime:"",
                    refundTime:"",
                    startDepTime:"",
                    endDepTime:"",
                    startValidateTime:"",
                    endValidateTime:"",
                    startRefundTime:"",
                    endRefundTime:"",
                },
                formLabelWidth: '120px',
                fullscreenLoading:false,
                orderStatus:{
                        1:"待支付",
                        2:"出票中",
                        3: "已出票",
                        4:"出票失败",
                        5:"已出行",
                        6:"退票",
                        7:"已取消"
                },
                payStatusOption:[
                    {
                        id:"1",
                        name:"未收款"
                    },{
                        id:"3",
                        name:"已收款"
                    },
                ],
                ticketStatusOption:[
                    {
                        id:"6",
                        name:"退票有手续费"
                    },{
                        id:"7",
                        name:"退票无手续费"
                    },{
                        id:"2",
                        name:"未出行"
                    },{
                        id:"4",
                        name:"已出行"
                    },
                ]
            }

        },
        methods: {
            handleCheckAllChange(val) {
                this.checkedCities = val ? cityOptions : [];
            },
            handleCheckedCitiesChange(value) {
                let checkedCount = value.length;
                this.checkAll = checkedCount === this.cities.length;
            },

            getOrderList: function () {

                var data = this.searchForm
                data.pageIndex = this.currentPage
                data.pageSize = this.pageSize

                for (var i in data) {
                    if (String(data[i]) == "") {
                        delete data[i]
                    }
                }

                ajax.orderFinance(data, res => {
                    if (res) {
                        this.listdata = res.data.result
                    }
                })
            },
            clear: function () {
                this.checkedCities = []
                this.checkAll = false
                var data = this.searchForm
                for (var i in data) {
                    data[i] = ""
                }
                this.value1 = ""
                this.value2 = ""

            },
            search: function () {
                this.searchForm.status = this.checkedCities.concat()

                this.currentPage = 1
                this.getOrderList()
            },
            godetail(serial, status) {
                //  this.$router.push({name:"gz_order_detail",query:{serial:serial,orderstatus:status,supplierId:""}})
                window.open("#/gz_order_detail?serial=" + serial + "&supplierId=")
            },
            orderExport: function () {
                var orderData = this.searchForm
                for (var i in orderData) {
                    if (String(orderData[i]) == "") {
                        delete orderData[i]
                    }
                }
                ajax.orderFinanceExp(orderData)
            },
            addDevice: function () {
                this.dialogFormVisible = true
            },
            getTimeValue1: function (val) {

                if (val == null) {
                    this.searchForm.startPayTime = ""
                } else {
                    this.searchForm.startPayTime = val
                }
            },
            getTimeValue2: function (val) {

                if (val == null) {
                    this.searchForm.endPayTime = ""
                } else {
                    this.searchForm.endPayTime = val
                }
            },
            getDepStationList(){
                ajax.getDepStationList({},res=>{
                    this.depStationlist = res.data.result

                })
            },

            handleSizeChange(val) {
                this.pageSize = val
                this.getOrderList()
            },
            handleCurrentChange(val) {
                this.currentPage = val
                this.getOrderList()
            },
            getadminsupplier(){
                ajax.adminsupplier({},res=>{

                    this.supplierOptions = res.data.result
                })
            }
        },
        mounted() {

            this.getOrderList()
            this.getDepStationList()
            this.getadminsupplier()
        }
    }
</script>
<style>
    .input-title {
        font-size: 14px !important;
        font-weight: 600;
    }

    .el-col {
        margin-left: 20px;
    }

    ::-webkit-scrollbar-thumb {
        background-color: gainsboro;
    }

    .cell {
        text-align: center;
    }

    #content-main {
        overflow: scroll;
    }

    .el-table {
        overflow: scroll;
    }

    .content-table {
        width: 100%;
        border-collapse: collapse;
        background-color: white;
        text-align: center;
    }

    .content-table th {
        height: 40px;
        border: 1px solid #F2F3F4;
        text-align: center;
        background-color: gainsboro;
    }

    .content-table td {
        height: 40px;
        border: 1px solid #F2F3F4;
    }

    .dothis {
        color: #3C9CFC;
        cursor: default;
        margin-left: 10px;
    }

    .page-f {
        margin-top: 20px;
        float: right;
    }

    .content-table span:hover {
        color: silver;
    }

    .el-col-23 {
        width: 99% !important;
    }
</style>



